export default function({...props}){
    return(
        <div className={props.className != null ? props.className : " col-12 col-md-6 col-lg-4"} >
            <div className="my-card">
                <div className="fs-5 text-center">
                    {props.title}
                </div>
                    {props.children}
            </div>
        </div>
    )
}